	module.exports = {"data":{"challengeNode":{"title":"Problem 89: Roman numerals","description":["For a number written in Roman numerals to be considered valid there are basic rules which must be followed. Even though the rules allow some numbers to be expressed in more than one way there is always a \"best\" way of writing a particular number.","For example, it would appear that there are at least six ways of writing the number sixteen:","IIIIIIIIIIIIIIII","VIIIIIIIIIII","VVIIIIII","XIIIIII","VVVI","XVI","However, according to the rules only XIIIIII and XVI are valid, and the last example is considered to be the most efficient, as it uses the least number of numerals.","The 11K text file, roman.txt (right click and 'Save Link/Target As...'), contains one thousand numbers written in valid, but not necessarily minimal, Roman numerals; see About... Roman Numerals for the definitive rules for this problem.","Find the number of characters saved by writing each of these in their minimal form.","Note: You can assume that all the Roman numerals in the file contain no more than four consecutive identical units."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-89-roman-numerals","blockName":"Project Euler","tests":[{"text":"<code>euler89()</code> should return 743.","testString":"assert.strictEqual(euler89(), 743, '<code>euler89()</code> should return 743.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler89() {\n  // Good luck!\n  return true;\n}\n\neuler89();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-90-cube-digit-pairs","id":"5900f3c51000cf542c50fed7"},"slug":"/coding-interview-prep/project-euler/problem-89-roman-numerals"}}

